:host {
  display: block;
}

.chat-footer-preview {
  background-color: var(--card-background-color, #ffffff);
  border-top: 1px solid var(--input-border-color, #e2e8f0);
  padding: 0.75rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.04);
}

.query-section {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.query-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.query-label {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--text-color-secondary, #64748b);
  text-transform: uppercase;
  letter-spacing: 0.025em;

  i {
    font-size: 0.875rem;
  }

  .edit-hint {
    font-size: 0.625rem;
    font-weight: 400;
    text-transform: none;
    color: var(--text-color-secondary, #94a3b8);
    opacity: 0;
    transition: opacity 0.2s ease;
  }
}

.confidence-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;

  &[data-level='high'] {
    .confidence-fill {
      background-color: #22c55e;
    }
    .confidence-text {
      color: #16a34a;
    }
  }

  &[data-level='medium'] {
    .confidence-fill {
      background-color: #eab308;
    }
    .confidence-text {
      color: #ca8a04;
    }
  }

  &[data-level='building'] {
    .confidence-fill {
      background-color: var(--color-primary-color, #3b82f6);
    }
    .confidence-text {
      color: var(--color-primary-color, #2563eb);
    }
  }

  &[data-level='low'] {
    .confidence-fill {
      background-color: var(--text-color-secondary, #94a3b8);
    }
    .confidence-text {
      color: var(--text-color-secondary, #64748b);
    }
  }
}

.confidence-bar {
  width: 60px;
  height: 4px;
  background-color: var(--input-border-color, #e2e8f0);
  border-radius: 9999px;
  overflow: hidden;
}

.confidence-fill {
  height: 100%;
  border-radius: 9999px;
  transition: width 0.3s ease;
}

.confidence-text {
  font-size: 0.6875rem;
  font-weight: 500;
  white-space: nowrap;
}

.query-content {
  background-color: var(--input-background-color, #ffffff);
  border: 1px solid var(--input-border-color, #e2e8f0);
  border-radius: 0.5rem;
  padding: 0.625rem 0.75rem;
  min-height: 2.5rem;
  max-height: 6rem;
  overflow-y: auto;

  &.empty {
    background-color: var(--surface-hover, #f1f5f9);
    border-style: dashed;
  }

  &.clickable {
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover {
      border-color: var(--color-primary-color, #3b82f6);
      background-color: var(--surface-hover, #f8fafc);
      box-shadow: 0 0 0 2px rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
    }

    &:focus {
      outline: none;
      border-color: var(--color-primary-color, #3b82f6);
      box-shadow: 0 0 0 2px rgba(var(--color-primary-rgb, 59, 130, 246), 0.2);
    }
  }
}

// Show edit hint on hover
.query-section:hover .edit-hint {
  opacity: 1;
}

.query-edit-container {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;

  .query-textarea {
    width: 100%;
    font-size: 0.8125rem;
    line-height: 1.5;
    resize: none;
    border: 1px solid var(--color-primary-color, #3b82f6);
    border-radius: 0.5rem;
    padding: 0.625rem 0.75rem;
    background-color: var(--input-background-color, #ffffff);
    color: var(--text-color, #1e293b);
    box-shadow: 0 0 0 2px rgba(var(--color-primary-rgb, 59, 130, 246), 0.2);

    &:focus {
      outline: none;
      border-color: var(--color-primary-color, #2563eb);
      box-shadow: 0 0 0 3px rgba(var(--color-primary-rgb, 59, 130, 246), 0.3);
    }
  }

  .edit-actions {
    display: flex;
    justify-content: flex-end;
    gap: 0.5rem;
  }
}

.query-text {
  margin: 0;
  font-size: 0.8125rem;
  color: var(--text-color, #334155);
  line-height: 1.5;
  word-break: break-word;
}

.query-placeholder {
  margin: 0;
  font-size: 0.8125rem;
  color: var(--text-color-secondary, #94a3b8);
  font-style: italic;
}

.collection-progress {
  ::ng-deep .collection-bar {
    height: 0.375rem;
    border-radius: 9999px;

    .p-progressbar-value {
      background: linear-gradient(
        90deg,
        var(--color-primary-color, #3b82f6),
        var(--color-primary-color, #60a5fa)
      );
    }
  }
}

.action-section {
  display: flex;
  justify-content: flex-end;

  ::ng-deep .generate-button {
    background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
    border: none;
    font-weight: 600;
    padding: 0.625rem 1.5rem;
    border-radius: 0.5rem;
    transition: all 0.2s ease;

    &:not(:disabled):hover {
      background: linear-gradient(135deg, #16a34a 0%, #15803d 100%);
      transform: translateY(-1px);
      box-shadow: 0 4px 12px rgba(34, 197, 94, 0.3);
    }

    &:disabled {
      background: var(--input-border-color, #e2e8f0);
      color: var(--text-color-secondary, #94a3b8);
      cursor: not-allowed;
    }

    .p-button-icon {
      margin-right: 0.5rem;
    }
  }
}

// Responsive adjustments
@media (max-width: 640px) {
  .chat-footer-preview {
    padding: 0.625rem 0.75rem;
  }

  .query-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.375rem;
  }

  .action-section {
    ::ng-deep .generate-button {
      width: 100%;
      justify-content: center;
    }
  }
}
